<template>
  <div class="royalty-page">
    <!-- 表头 -->
    <div class="table-header ba-scroll-style">
      <!-- 刷新按钮 -->
      <el-button :icon="Refresh" @click="refreshData" color="#40485b"/>

      <!-- 搜索按钮 -->
      <div class="table-search">
        <el-button-group class="ml-4">
          <el-button :icon="Search" @click="toggleSearch" />
        </el-button-group>
      </div>

      <!-- 批量操作 -->
      <div class="table-actions" v-if="selectedRows.length > 0">
        <el-button 
          type="warning" 
          :icon="RefreshRight" 
          @click="handleBatchRetry"
          :disabled="!hasFailedSelected"
        >
          批量重试（{{ selectedRows.length }}）
        </el-button>
      </div>
    </div>

    <!-- 搜索表单 -->
    <div v-if="searchVisible" class="search-form ba-scroll-style">
      <el-form :inline="true" :model="searchForm" class="demo-form-inline">
        <el-form-item label="平台订单号">
          <el-input v-model="searchForm.platform_order_no" placeholder="请输入平台订单号" clearable/>
        </el-form-item>
        <el-form-item label="交易号">
          <el-input v-model="searchForm.trade_no" placeholder="请输入支付宝交易号" clearable/>
        </el-form-item>
        <el-form-item label="分账状态">
          <el-select v-model="searchForm.royalty_status" placeholder="请选择" clearable>
            <el-option label="待分账" value="0"/>
            <el-option label="分账中" value="1"/>
            <el-option label="分账成功" value="2"/>
            <el-option label="分账失败" value="3"/>
          </el-select>
        </el-form-item>
        <el-form-item label="分账类型">
          <el-select v-model="searchForm.royalty_type" placeholder="请选择" clearable>
            <el-option label="不分账" value="none"/>
            <el-option label="单笔分账" value="single"/>
            <el-option label="商家分账" value="merchant"/>
          </el-select>
        </el-form-item>
        <el-form-item label="时间范围">
          <el-date-picker
            v-model="dateRange"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始时间"
            end-placeholder="结束时间"
            format="YYYY-MM-DD HH:mm:ss"
            value-format="YYYY-MM-DD HH:mm:ss"
          />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch">搜索</el-button>
          <el-button @click="handleReset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 表格内容 -->
    <div style="width: 100%; overflow-x: auto;" class="table-content">
      <el-table
        :data="data.data"
        border
        stripe
        max-height="600"
        style="width: 100%;"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="40"/>
        
        <!-- 表格列定义 -->
        <el-table-column prop="platform_order_no" label="平台订单号" align="center" min-width="200"/>
        <el-table-column prop="trade_no" label="交易号" align="center" min-width="200"/>
        <el-table-column prop="order.agent.agent_name" label="代理商" align="center" min-width="120"/>
        <el-table-column prop="order.merchant.merchant_name" label="商户" align="center" min-width="150"/>
        
        <!-- 分账类型列 -->
        <el-table-column label="分账类型" align="center" min-width="100">
          <template #default="{ row }">
            <el-tag v-if="row.royalty_type === 'none'" type="info">不分账</el-tag>
            <el-tag v-else-if="row.royalty_type === 'single'" type="success">单笔分账</el-tag>
            <el-tag v-else-if="row.royalty_type === 'merchant'" type="warning">商家分账</el-tag>
          </template>
        </el-table-column>
        
        <!-- 分账金额列 -->
        <el-table-column label="分账金额" align="center" min-width="120">
          <template #default="{ row }">
            <span v-if="row.royalty_amount > 0">¥{{ row.royalty_amount }}</span>
            <span v-else>-</span>
          </template>
        </el-table-column>
        
        <!-- 主体收款金额列 -->
        <el-table-column label="主体收款" align="center" min-width="120">
          <template #default="{ row }">
            ¥{{ row.subject_amount }}
          </template>
        </el-table-column>
        
        <!-- 收款人信息列 -->
        <el-table-column label="收款人" align="center" min-width="150">
          <template #default="{ row }">
            <div v-if="row.payee_name">
              <div>{{ row.payee_name }}</div>
              <div style="font-size: 12px; color: #999;">{{ row.payee_user_id || row.payee_account }}</div>
            </div>
            <span v-else>-</span>
          </template>
        </el-table-column>
        
        <!-- 分账状态列 -->
        <el-table-column label="分账状态" align="center" min-width="100">
          <template #default="{ row }">
            <el-tag v-if="row.royalty_status === 0" type="info">待分账</el-tag>
            <el-tag v-else-if="row.royalty_status === 1" type="warning">分账中</el-tag>
            <el-tag v-else-if="row.royalty_status === 2" type="success">分账成功</el-tag>
            <el-tag v-else-if="row.royalty_status === 3" type="danger">分账失败</el-tag>
          </template>
        </el-table-column>
        
        <!-- 分账时间列 -->
        <el-table-column prop="royalty_time" label="分账时间" align="center" min-width="160"/>
        <el-table-column prop="created_at" label="创建时间" align="center" min-width="160"/>

        <!-- 操作列 -->
        <el-table-column label="操作" align="center" fixed="right" min-width="150">
          <template #default="{ row }">
            <el-button size="small" @click="handleViewDetail(row)">详情</el-button>
            <el-button 
              v-if="row.royalty_status === 3" 
              size="small" 
              type="warning" 
              @click="handleRetry(row)"
            >
              重试
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 表格分页 -->
    <div class="table-footer">
      <div class="table-pagination">
        <el-pagination
          v-model:current-page="data.current_page"
          v-model:page-size="data.page_size"
          :page-sizes="[10, 50, 100, 200]"
          :background="true"
          prev-text="上一页"
          next-text="下一页"
          layout="total, sizes, prev, pager, next, jumper"
          :total="data.page_total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>

    <!-- 详情对话框 -->
    <el-dialog v-model="detailVisible" title="分账详情" width="800px">
      <el-descriptions :column="2" border v-if="currentRoyalty">
        <el-descriptions-item label="平台订单号">{{ currentRoyalty.platform_order_no }}</el-descriptions-item>
        <el-descriptions-item label="交易号">{{ currentRoyalty.trade_no || '-' }}</el-descriptions-item>
        <el-descriptions-item label="订单金额">¥{{ currentRoyalty.order?.order_amount || '-' }}</el-descriptions-item>
        <el-descriptions-item label="分账类型">
          <el-tag v-if="currentRoyalty.royalty_type === 'none'" type="info">不分账</el-tag>
          <el-tag v-else-if="currentRoyalty.royalty_type === 'single'" type="success">单笔分账</el-tag>
          <el-tag v-else-if="currentRoyalty.royalty_type === 'merchant'" type="warning">商家分账</el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="分账比例">{{ currentRoyalty.royalty_rate }}%</el-descriptions-item>
        <el-descriptions-item label="分账金额">¥{{ currentRoyalty.royalty_amount || 0 }}</el-descriptions-item>
        <el-descriptions-item label="主体收款">¥{{ currentRoyalty.subject_amount }}</el-descriptions-item>
        <el-descriptions-item label="分账状态">
          <el-tag v-if="currentRoyalty.royalty_status === 0" type="info">待分账</el-tag>
          <el-tag v-else-if="currentRoyalty.royalty_status === 1" type="warning">分账中</el-tag>
          <el-tag v-else-if="currentRoyalty.royalty_status === 2" type="success">分账成功</el-tag>
          <el-tag v-else-if="currentRoyalty.royalty_status === 3" type="danger">分账失败</el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="分账时间">{{ currentRoyalty.royalty_time || '-' }}</el-descriptions-item>
        <el-descriptions-item label="收款人姓名">{{ currentRoyalty.payee_name || '-' }}</el-descriptions-item>
        <el-descriptions-item label="收款人账号">{{ currentRoyalty.payee_account || currentRoyalty.payee_user_id || '-' }}</el-descriptions-item>
        <el-descriptions-item label="分账单号">{{ currentRoyalty.alipay_royalty_no || '-' }}</el-descriptions-item>
        <el-descriptions-item label="创建时间">{{ currentRoyalty.created_at }}</el-descriptions-item>
        <el-descriptions-item label="更新时间">{{ currentRoyalty.updated_at }}</el-descriptions-item>
        <el-descriptions-item v-if="currentRoyalty.royalty_error" label="失败原因" :span="2">
          <span style="color: #f56c6c;">{{ currentRoyalty.royalty_error }}</span>
        </el-descriptions-item>
        <el-descriptions-item v-if="currentRoyalty.alipay_result_parsed" label="支付宝返回结果" :span="2">
          <pre style="background: #f5f5f5; padding: 10px; border-radius: 4px; max-height: 300px; overflow: auto;">
            {{ JSON.stringify(currentRoyalty.alipay_result_parsed, null, 2) }}
          </pre>
        </el-descriptions-item>
      </el-descriptions>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, computed, onMounted } from 'vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import { Refresh, Search, RefreshRight } from '@element-plus/icons-vue';
import request from '@/utils/request';
import { useTableDataPlugin } from "@/utils/plugins/table.plugin.ts";

const {
  data,
  refreshData,
  handleSizeChange,
  handleCurrentChange,
  searchParams
} = useTableDataPlugin("/api/v1/admin/royalty/list");

const searchVisible = ref(false);
const searchForm = reactive({
  platform_order_no: '',
  trade_no: '',
  royalty_status: '',
  royalty_type: '',
});

const dateRange = ref<[string, string] | null>(null);

const selectedRows = ref<any[]>([]);
const detailVisible = ref(false);
const currentRoyalty = ref<any>(null);

// 是否有失败的分账记录被选中
const hasFailedSelected = computed(() => {
  return selectedRows.value.some(row => row.royalty_status === 3);
});

const toggleSearch = () => {
  searchVisible.value = !searchVisible.value;
};

const handleSearch = async () => {
  const params: any = { ...searchForm };
  if (dateRange.value) {
    params.start_time = dateRange.value[0];
    params.end_time = dateRange.value[1];
  }
  searchParams.value = params;
  await refreshData();
};

const handleReset = () => {
  Object.assign(searchForm, {
    platform_order_no: '',
    trade_no: '',
    royalty_status: '',
    royalty_type: '',
  });
  dateRange.value = null;
  searchParams.value = {};
  refreshData();
};

const handleSelectionChange = (selection: any[]) => {
  selectedRows.value = selection;
};

const handleViewDetail = async (row: any) => {
  try {
    const res: any = await request({
      url: `/api/v1/admin/royalty/detail/${row.id}`,
      method: 'GET'
    });
    if (res.status) {
      currentRoyalty.value = res.data;
      detailVisible.value = true;
    }
  } catch (error) {
    console.error('获取详情失败:', error);
    ElMessage.error('获取详情失败');
  }
};

const handleRetry = async (row: any) => {
  try {
    await ElMessageBox.confirm(
      `确定要重试分账吗？\n订单号：${row.platform_order_no}`,
      '重试分账',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }
    );

    const res: any = await request({
      url: `/api/v1/admin/royalty/retry/${row.id}`,
      method: 'POST'
    });

    if (res.status) {
      ElMessage.success('重试成功');
      await refreshData();
    } else {
      ElMessage.error(res.msg || '重试失败');
    }
  } catch (error: any) {
    if (error !== 'cancel') {
      console.error('重试失败:', error);
      ElMessage.error(error.message || '重试失败');
    }
  }
};

const handleBatchRetry = async () => {
  if (selectedRows.value.length === 0) {
    ElMessage.warning('请选择要重试的分账记录');
    return;
  }

  const failedRows = selectedRows.value.filter(row => row.royalty_status === 3);
  if (failedRows.length === 0) {
    ElMessage.warning('所选记录中没有失败的分账记录');
    return;
  }

  try {
    await ElMessageBox.confirm(
      `确定要批量重试 ${failedRows.length} 条失败的分账记录吗？`,
      '批量重试',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }
    );

    const ids = failedRows.map(row => row.id);
    const res: any = await request({
      url: '/api/v1/admin/royalty/batch-retry',
      method: 'POST',
      data: { ids }
    });

    if (res.status) {
      ElMessage.success(`批量重试完成：成功 ${res.data.success_count} 条，失败 ${res.data.failed_count} 条`);
      await refreshData();
      selectedRows.value = [];
    } else {
      ElMessage.error(res.msg || '批量重试失败');
    }
  } catch (error: any) {
    if (error !== 'cancel') {
      console.error('批量重试失败:', error);
      ElMessage.error(error.message || '批量重试失败');
    }
  }
};

onMounted(() => {
  refreshData();
});
</script>

<style scoped>
.royalty-page {
  padding: 20px;
}

.search-form {
  margin-bottom: 20px;
  padding: 20px;
  background: #fff;
  border-radius: 4px;
}

.table-content {
  margin-bottom: 20px;
}

.table-footer {
  display: flex;
  justify-content: flex-end;
}
</style>

